import { useState } from 'react'
import useSWR from 'swr'
import Button from '@material-ui/core/Button'
import { makeStyles } from '@material-ui/core/styles'
import GridList from '@material-ui/core/GridList'
import GridListTile from '@material-ui/core/GridListTile'
import Image from 'material-ui-image'

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 500,
    height: 450,
  },
}))

const Images = (props) => {
  console.log('🚹🚺🚻🛏️🚼 ~ file: index.js ~ line 7 ~ Image ~ props', props)
  const [picUrl, setPicUrl] = useState(null)
  const [jsonUrl, setJsonUrl] = useState(null)
  const classes = useStyles()

  const [page, setPage] = useState(1)
  const { data: jsonArr } = useSWR(jsonUrl)
  const { data: pics } = useSWR(`https://picsum.photos/v2/list?page=${page}`)
  console.log('🚹🚺🚻🛏️🚼 ~ file: index.js ~ line 12 ~ Image ~ pics', pics)

  const handleClick1 = () => {
    setPicUrl('https://picsum.photos/v2/list')
  }

  const handleClick2 = () => {
    setJsonUrl('https://jsonplaceholder.typicode.com/todos')
  }

  return (
    <div>
      <Button variant='contained' color='primary' onClick={handleClick1}>
        Fetch Image
      </Button>
      <Button variant='secondary' color='primary' onClick={handleClick2}>
        fetch data
      </Button>
      <Button variant='secondary' color='primary' onClick={() => setPage((c) => ++c)}>
        +1
      </Button>
      <Button
        variant='secondary'
        color='primary'
        onClick={() => {
          if (page > 1) return setPage((c) => --c)
        }}
      >
        -1
      </Button>
      <div className={classes.root}>
        <GridList cellHeight={160} className={classes.gridList} cols={3}>
          {pics &&
            pics.map((pic) => (
              <GridListTile key={pic.download_url} cols={pic.cols || 1}>
                <Image src={pic.download_url} alt={pic.title} />
              </GridListTile>
            ))}
        </GridList>
        {/* <div>{pics && pics.map((item, idx) => <img key={idx} src={item.download_url} />)}</div> */}
        {jsonArr ? JSON.stringify(jsonArr) : null}
      </div>
    </div>
  )
}

export default Images
